import React from 'react'
import styles from './heading.less'

export default function Heading({ title, des, en, useWhite = false, splitClassName = '' }) {
  return (
    <div className={`${styles.heading} ${useWhite ? styles.white : ''}`}>
      <div className={styles.title}>{title}</div>
      {!!des && <div className={styles.des}>{des}</div>}
      <div className={`${styles.split} ${splitClassName}`} />
      <div className={styles.en}>{en}</div>
    </div>
  )
}
