/* eslint-disable global-require */
import React, { useState } from 'react';
import axios from 'axios';
import { notify } from 'react-notify-toast';
import Heading from '@/components/heading';
import RequestServiceSuccessModal from '@/components/requestServiceSuccessModal';
import { contact } from 'data/global';
import styles from './contact.less';

export default function ContactPage() {
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [comment, setComment] = useState('');

  const [visible, setVisible] = useState(false);

  const onSubmit = () => {
    if (!phone.trim()) return notify.show('请输入联系电话', 'error');

    axios.post('/api/common/leave/message', { name, phone, email, comment }).then(
      result => {
        setVisible(true);
        setName('');
        setPhone('');
        setEmail('');
        setComment('');
      },
      error => {
        notify.show('服务器内部错误，请稍后重试', 'error');
        console.log(error);
      },
    );
  };

  return (
    <div className={styles.contactContainer}>
      <div className="center-block">
        <div className="block-left">
          <Heading title={contact.title} des={contact.desc} en="contact us" />
        </div>
        <div className="block-right">
          <div className={styles.box}>
            <div className={styles.tip}>
              {contact.emailTip}
              <a href={`mailto:${contact.email}`}>{contact.email}</a>
              {contact.phoneTip}
              <a href={`tel:${contact.phone}`}>{contact.phone} </a>
            </div>
            <img src={require('@/static/images/postmark.png')} className={styles.flag} alt="" />
            <div className={styles.inputGroup}>
              <input
                maxLength="100"
                type="text"
                className={styles.input}
                value={name}
                onChange={e => {
                  setName(e.target.value);
                }}
                placeholder="您的姓名"
              />
              <input
                maxLength="11"
                type="text"
                className={styles.input}
                value={phone}
                onChange={e => {
                  setPhone(e.target.value);
                }}
                placeholder="您的联系电话"
              />
              <input
                maxLength="100"
                type="text"
                className={styles.input}
                value={email}
                onChange={e => {
                  setEmail(e.target.value);
                }}
                placeholder="您的联系邮箱"
              />
            </div>
            <textarea
              maxLength="500"
              className={styles.textarea}
              value={comment}
              onChange={e => {
                setComment(e.target.value);
              }}
              placeholder="请大致表达您的合作意向，我们会在24小时内与您取得联系"
            />
            <div className={styles.btnLine}>
              <span onClick={onSubmit} className={styles.button}>
                完成并提交
              </span>
            </div>
          </div>
        </div>
      </div>
      <RequestServiceSuccessModal
        visible={visible}
        onClose={() => {
          setVisible(false);
        }}
      />
    </div>
  );
}
