
import React from 'react'

function formatThousands(num) {
  if (Number.isNaN(+num)) {
    return num
  }
  const str = String(num)
  let int = str
  let decimal = ''
  const ps = str.split('.')
  if (ps.length > 1) {
    ;[int, decimal] = ps
  }

  int = int
    .split('')
    .reverse()
    .join('')
    .replace(/(\d{3})/g, '$1,')
    .replace(/,+$/, '')
    .split('')
    .reverse()
    .join('')

  return int + (decimal ? `.${decimal}` : '')
}

export default class NumberFormarter extends React.Component {
  state = {
    animate: false,
    thousands: false,
    value: 0,
    current: 0,
    interval: 50,
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const newState = {}
    if (nextProps.value !== prevState.value) {
      newState.value = nextProps.value
      if (!nextProps.animate) {
        newState.current = nextProps.value
      }
    }
    if (nextProps.animate !== prevState.animate) {
      newState.animate = nextProps.animate
    }
    if (nextProps.thousands !== prevState.thousands) {
      newState.thousands = nextProps.thousands
    }
    if (Object.keys(newState).length > 0) {
      return newState
    }
    return null
  }

  componentDidMount() {
    const { animate, value, current } = this.state
    if (animate) {
      this.animate(current, value)
    }
  }

  componentDidUpdate(_, prevState) {
    const { animate, value, current } = this.state
    if (animate && prevState.value !== value) {
      this.animate(current, value)
    }
  }

  animate = (fromNumber, toNumber) => {
    const { interval } = this.state
    const plus = toNumber > fromNumber
    let addNum = (toNumber - fromNumber) / interval
    if (plus && addNum < 1) {
      addNum = 1
    } else if (!plus && addNum > -1) {
      addNum = -1
    }
    const step = () => {
      fromNumber = +(fromNumber + addNum).toFixed(0, 10)
      this.setState(
        {
          current: plus
            ? fromNumber > toNumber
              ? toNumber
              : fromNumber
            : fromNumber < toNumber
            ? toNumber
            : fromNumber,
        },
        () => {
          if ((plus && fromNumber < toNumber) || (!plus && fromNumber > toNumber)) {
            window.requestAnimationFrame(step)
          }
        }
      )
    }
    window.requestAnimationFrame(step)
  }

  render() {
    let { current } = this.state
    const { value, thousands, animate, prefix, ...restProps } = this.props // eslint-disable-line
    if (thousands) {
      current = formatThousands(current)
    }
    return (
      <span {...restProps}>
        {prefix}
        {current}
      </span>
    )
  }
}
