import React from 'react';
import Number from '@/components/number';
import { banner } from 'data/about';

import styles from './index.less';

export default function Banner() {
  return (
    <div className={styles.banner}>
      <div className={styles.content}>
        <div className={styles.title}>{banner.title}</div>
        <div className={styles.desc}>{banner.desc}</div>
      </div>
      <div className={`${styles.countWrap} center`}>
        {banner.dataSource.map((dataSource, index) => {
          return (
            <div key={index} className={styles.item}>
              <div className={styles.count}>
                <Number value={dataSource.value} animate thousands />
                {dataSource.unit}
              </div>
              <div className={styles.desc}>{dataSource.title}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
